  margin: 0;
  color: #89316e;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px; }

@media screen and (min-width: 768px) {
  .block1__list li {
    width: 49%; } }

.block2 {
  padding: 130px 15px 0;
  margin: -100px 0 0;
  background-image: url("../img/block2-back.jpg");
  overflow: hidden; }

.block2__wrapper {
  max-width: 1170px;
  margin: auto; }

.block2__content {
  position: relative;
  max-width: 940px;
  padding: 15px;
  background-color: #ffffff91; }
  .block2__content::after {
    content: "";
    position: absolute;
    bottom: 0;
    right: -360px;
    width: 70%;
    height: 500px;
    background-image: url("../img/uzi-img.png");
    background-repeat: no-repeat;
    background-position: center; }

.block2__title {
  font-size: 36px;
  font-weight: 700;
  line-height: 40px;
  color: #89316E; }

.block2__text {
  font-weight: 300;
  font-size: 18px;
  line-height: 24px;
  color: #5D5D61; }

@media screen and (min-width: 768px) {
  .block2 {
    padding: 130px 25px 0; }
  .block2__content {
    padding: 10px 236px 10px 60px;
    background-color: #ffffff; } }

.block3 {
  padding: 10px 25px;
  background-color: #FFEDB8; }

.block3__wrapper {
  max-width: 1170px;
  margin: auto; }

.block3__title {
  font-size: 36px;
  font-weight: 700;
  line-height: 40px;
  color: #89316E;
  text-align: center; }

.block3__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }
  .block3__list li {
    position: relative;
    margin-bottom: 3.45%;
    padding: 10px 30px;
    background-color: #ffffff;
    color: #474747; }
    .block3__list li::before {
      content: "";
      position: absolute;
      top: 40px;
      left: -15px;
      width: 31px;
      height: 31px;
      background: #F9F2DD;
      transform: rotate(45deg);
      border: 6px solid #FF5F00; }

.block3__item-title {
  font-size: 18px;
  font-weight: 400px;
  line-height: 20px; }

.block3__item-text {
  color: #89316e;
  font-size: 16px;
  font-weight: 300;
  line-height: 20px; }

@media screen and (min-width: 768px) {
  .block3__list li {
    width: 31%; } }

.block4 {
  padding: 130px 15px 0;
  background-image: linear-gradient(180deg, #FFE8EE 0%, #E7E6EF 100%);
  overflow: hidden; }

.block4__wrapper {
  max-width: 1170px;
  margin: auto; }

.block4__content {
  position: relative;
  max-width: 670px;
  padding: 15px;
  margin-left: auto;
  background-color: #ffffff91; }

.block4__title {
  font-size: 36px;
  font-weight: 700;
  line-height: 40px;
  color: #89316E; }

.block4__text {
  font-weight: 300;
  font-size: 18px;
  line-height: 24px;
  color: #5D5D61; }

@media screen and (min-width: 768px) {
  .block4 {
    padding: 30px;
    background-image: url("../img/back4.png"), linear-gradient(180deg, #FFE8EE 0%, #E7E6EF 100%);
    background-repeat: no-repeat;
    background-position: top left -250px, center; }
  .block4__content {
    padding: 10px 60px 10px;
    background-color: #ffffff; } }

.callback {
  max-width: 900px;
  margin: auto;
  padding: 10px 25px;
  background-image: url("../img/img2.webp");
  background-repeat: no-repeat;
  background-position: bottom left; }

.callback__title {
  font-size: 36px;
  font-weight: 700;
  line-height: 40px;
  color: #89316E;
  text-align: center; }

.callback__form {
  width: 421px;
  margin-left: auto;
  margin-bottom: 10px;
  padding: 10px 61px 30px;
  background: #F9F2DD; }

.callback__text {
  font-size: 18px;
  font-weight: 300;
  line-height: 20px;
  color: #89316E; }

.callback__input {
  width: 100%;
  padding: 17px 15px 17px 40px;
  margin-bottom: 20px;
  font-family: 'Roboto', 'Tahoma', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  border: none;
  box-shadow: inset 0px 1px 2px rgba(0, 0, 0, 0.37);
  background-image: url("../img/user.svg");
  background-repeat: no-repeat;
  background-position: center left 10px; }
  .callback__input--tel {
    background-image: url("../img/phone.svg"); }
  .callback__input--text {
    min-height: 136px;
    background-position: top 20px left 10px;
    background-image: url("../img/twit.svg");
    resize: none; }
  .callback__input::placeholder {
    color: #C8AE99; }

.callback__btn {
  width: 100%;
  padding: 20px 30px;
  font-family: 'Roboto', 'Tahoma', sans-serif;
  color: #ffffff;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  letter-spacing: 3px;
  text-transform: uppercase;
  border: none;
  background-image: linear-gradient(90deg, #FF5D00 0%, #FE8100 100%); }
